import React, { Component } from 'react';
import CardList from './CardList';
import {subfields} from './subfields';
import SearchBox from './searchbox'
import './index.css'
import Scroll from './Scroll';

const state = {
	subfields: subfields,
	searchfield: ''
}

class App extends Component {

	constructor() {
		super()
		this.state = {
			subfields: subfields,
			searchfield: ''
		}
	}

	onSearchChange = (event) => {
		this.setState({ searchfield: event.target.value})
		
	}

	render() {
		const filteredSubfields = this.state.subfields.filter( subfields => {
				return subfields.name.toLowerCase().includes(this.state.searchfield.toLowerCase());
			}
	    )
		return (
			<div className='tc'>
				<div>
				    <header className='tc pv4 pv5-ns'>
				       <img className='index_image' src={require('./assets/ai.jpg')} />
				        <h1 className='f2 light-green'>Všechny tváře umělé inteligence</h1>
				        <h2 className='f3 light-green'>Přehled pro inspiraci</h2>
				    </header>
				 
				 	<SearchBox searchChange={this.onSearchChange}/>
			    </div>
			    {/*<Scroll>*/}
					<CardList subfields={filteredSubfields}/>
				{/*</Scroll>*/}
			</div>
		);
	}
}

export default App;