import React from 'react';
import Card from './Card.js';

const CardList = ({ subfields }) => {
	return (
		<div>
			{subfields.map((user, i) => {
				return (
					<Card 
						key={i} 
						id={subfields[i].id} 
						name={subfields[i].name} 
						link={subfields[i].link} 
						image_path={subfields[i].image_path}
					/>
				);
			})}
		</div>
		);
}

export default CardList;