import React from 'react';
import "./Card.css";
import regrese from './assets/regrese.png'

const Card = (props) => {
	const { id, name, link, image_path} = props
	return (
		<div className='tc bg-light-blue dib br3 pa3 ma2 grow bw2 shadow-5'>
			<a href={link}><img className="card_image" src={image_path} /></a>
			<div>
				<h2>{name}</h2>
			</div>
		</div>
	);
}

export default Card;